.Persik {
    margin:auto auto auto auto;
    height: 80vmin; /* высота картинки */
    display:flex;
    align-items: center;
    justify-content: center;
    margin-top: 5vmin;

}
.noselect {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}


.menu img {
    width: 60px;
    position: fixed;
    bottom:10px;
    right: 10px;
    height: 60px;

    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.imgret {
    background: #fff; /* Цвет фона */
    color: #fff; /* Цвет текста */
    padding: 0px; /* Поля вокруг текста */
    min-height: 18%;
    min-width: 18%;

    border-radius: 20px; /* Уголки */
}
.cover {
   max-width: 100%;

}